import React from 'react';
import Context from '../../Context';
import classes from '../Navbar/Navbar.module.scss';

function Menu() {
	return (
		<React.Fragment>
			{localStorage.getItem('auth') !== null ? (
				<Context.Consumer>
					{(value) => (
						<div onClick={() => value.openSidebar()} className="mr-4">
							<img
								className={classes.navbaritems}
								alt="menu"
								src={require(value.sidebar ? '../../img/multiply.png' : '../../img/menu.png')}
							/>
						</div>
					)}
				</Context.Consumer>
			) : null}
		</React.Fragment>
	);
}

export default Menu;
