import React from 'react';
import classes from '../Navbar/Navbar.module.scss';
import { withRouter } from 'react-router-dom';

function SignInSignOut({ history }) {
	const [ clicked, setClicked ] = React.useState(false);
	return (
		<React.Fragment>
			<img
				onClick={() => setClicked((prev) => !prev)}
				className={classes.profile}
				alt="admin"
				src={require(localStorage.getItem('auth') === null ? '../../img/profile.png' : '../../img/admin.jpg')}
			/>
			{clicked ? (
				<div className={classes.profileClick}>
					<button
						className={classes.signout}
						onClick={() => {
							localStorage.removeItem('auth');
							history.push('/login');
							setClicked((prev) => !prev);
						}}
					>
						{localStorage.getItem('auth') !== null ? 'خروج' : 'ورود'}
					</button>
				</div>
			) : null}
		</React.Fragment>
	);
}

export default withRouter(SignInSignOut);
