import React, { Suspense, lazy } from 'react';
import Context from './Context';
import Navbar from './components/Navbar/Navbar';
import { Route, Switch, withRouter } from 'react-router-dom';
import { Spin } from 'antd';
const Login = lazy(() => import('./components/Login/Login'));
const SidebarAndOther = lazy(() => import('./components/SidebarAndOther/SidebarAndOther'));

function App({ history }) {
	const [ sidebar, setSidebar ] = React.useState(false);
	const openSidebar = () => {
		setSidebar((prev) => !prev);
	};
	React.useEffect(() => {
		localStorage.getItem('auth') !== null ? history.push('/') : history.push('/login');
	}, []);
	return (
		<React.Fragment>
			<Context.Provider value={{ sidebar, openSidebar }}>
				<Navbar />
				<Suspense
					fallback={
						<div className="d-flex justify-content-center align-items-center mt-5">
							<Spin />
						</div>
					}
				>
					<Switch>
						<Route exact path="/" component={SidebarAndOther} />
						<Route exact path="/login" component={Login} />
					</Switch>
				</Suspense>
			</Context.Provider>
		</React.Fragment>
	);
}

export default withRouter(App);
